.ardocs-menu--offcanvas {
  height: 100%;
  width: 300px;
  transform: translate3d(-100%, 0px, 0px);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  animation-duration: 400ms;
}
body.offcanvas-open {
  overflow-x: hidden;
  background: #0093ff;
}
body.offcanvas-open #ardocs-container {
  transform: translate3d(300px, 0px, 0px);
  transition: 300ms;
}
body.offcanvas-open #ardocs-container > * {
  opacity: 0.3;
}
body.offcanvas-open .ardocs-menu--offcanvas {
  background: #0093ff;
  transform: translate3d(0, 0px, 0px);
}
body.offcanvas-open .ardocs-menu--offcanvas header {
  background-color: #0086ec;
  width: 100%;
  height: 82px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
body.offcanvas-open .ardocs-menu--offcanvas header .offcanvas-close {
  position: absolute;
  top: calc(50% - 10px);
  left: 24px;
  cursor: pointer;
}
body.offcanvas-open .ardocs-menu--offcanvas header .offcanvas-close i {
  font-size: 24px;
  color: #fff;
}
body.offcanvas-open .ardocs-menu--offcanvas nav {
  padding: 0 24px 24px 24px;
}
body.offcanvas-open .ardocs-menu--offcanvas .ardocs-menu__title {
  font-size: 26px;
  color: #fff;
  margin: 30px 0 10px 0;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu {
  list-style-type: none;
  display: flex;
  flex-flow: column wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0;
  padding: 0 0 30px 0;
  border-bottom: 1px solid rgba(149, 196, 241, 0.8);
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu > li a {
  color: rgba(255, 255, 255, 0.8);
  font-size: 15px;
  line-height: 45px;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu > li a i {
  margin-right: 4px;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.is-active a {
  color: #fff;
  font-weight: bold;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li .submenu {
  display: none;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.dropdown {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.dropdown .submenu {
  margin-left: 24px;
  padding: 0;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.dropdown .dropdown__arrow {
  position: absolute;
  right: 10px;
  top: 10px;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.dropdown .dropdown__arrow:after {
  display: inline-block;
  font: normal normal normal 15px/1 'Material-Design-Iconic-Font';
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: '\f2f9';
  color: rgba(255, 255, 255, 0.8);
  margin-left: 10px;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.dropdown:hover .dropdown__arrow {
  display: block;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.dropdown--open {
  align-items: center;
  flex-flow: column wrap;
  align-items: flex-start;
  display: flex;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu li.dropdown--open .submenu {
  display: flex;
  flex-flow: column nowrap;
}
body.offcanvas-open .ardocs-menu--offcanvas ul.ardocs-main-menu ul {
  list-style-type: none;
  display: flex;
  flex-flow: column wrap;
  align-items: flex-start;
  justify-content: flex-start;
}
