@media only screen and (min-width: 768px) {
  .ardocs-main-menu {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-flow: row nowrap;
  }
  .ardocs-main-menu > li {
    padding-right: 40px;
    line-height: 80px;
    cursor: pointer;
  }
  .ardocs-main-menu > li:last-child {
    padding-right: 0;
  }
  .ardocs-main-menu > li.dropdown {
    position: relative;
  }
  .ardocs-main-menu > li.dropdown:after {
    display: inline-block;
    font: normal normal normal 15px/1 'Material-Design-Iconic-Font';
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: '\f2f9';
    color: #8d8d8d;
    margin-left: 10px;
  }
  .ardocs-main-menu > li.dropdown:hover .submenu {
    display: flex;
  }
  .ardocs-main-menu > li.dropdown:hover .dropdown__arrow {
    display: block;
  }
  .ardocs-main-menu > li.is-active a,
  .ardocs-main-menu > li.is-active i {
    color: #0093ff;
  }
  .ardocs-main-menu > li > a {
    line-height: 80px;
    display: inline-block;
  }
  .ardocs-main-menu a {
    color: #8d8d8d;
  }
  .ardocs-main-menu a:hover {
    font-weight: bold;
  }
  .ardocs-main-menu a i {
    font-size: 16px;
    color: #0093ff;
    margin-right: 10px;
  }
  .ardocs-main-menu .dropdown__arrow {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 5px 7.5px 5px;
    border-color: transparent transparent rgba(141, 141, 141, 0.35) transparent;
    content: '';
    position: absolute;
    top: calc(100% - 7.5px);
    right: 0;
    display: none;
  }
  .ardocs-main-menu ul.submenu {
    list-style-type: none;
    margin: 0;
    padding: 10px 0px;
    align-items: center;
    justify-content: flex-end;
    flex-flow: column nowrap;
    display: none;
    min-width: 150%;
    text-align: center;
    line-height: 36px;
    position: absolute;
    top: 100%;
    right: -10px;
    background: #fff;
    z-index: 2;
    box-shadow: 2px 0 35px rgba(0, 0, 0, 0.16);
  }
  .ardocs-main-menu ul.submenu > li {
    min-width: 100%;
  }
  .ardocs-main-menu ul.submenu > li a:hover {
    font-weight: bold;
  }
}
