.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.easy-cta {
  background: url(../img/circuit.png) #5dc560;
  padding: 55px 0;
  align-items: center;
  display: flex;
  justify-content: center;
}
.easy-cta .set-width {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}
.easy-cta .header-top {
  font-family: robotolight;
  font-size: 38px;
  line-height: 38px;
  color: #fff;
  font-weight: 300;
  margin: 0;
  margin-bottom: 8px;
}
.easy-cta .header-bottom {
  font-family: robotolight;
  font-size: 21px;
  line-height: 21px;
  color: #ceffcc;
  margin-bottom: 0;
  font-weight: 300;
}
footer.easy {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  background: #39425c;
  font-family: robotolight;
  font-weight: 300;
  font-size: 15px;
  color: #989eb2;
  line-height: 80px;
  min-height: 80px;
  border-top: 0;
}
footer.easy .set-width {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
footer.easy .set-width > div:first-child p,
footer.easy .set-width > div:first-child a {
  color: #989eb2;
  font-size: 15px;
  font-weight: 300;
